import React, { useEffect } from "react"
import { makeStyles } from "@material-ui/styles"
import { Theme } from "@material-ui/core/styles"
import { SlugsInfo } from "../../../generated/ProjectPerYear"
import NavAndSideBarLayout from "../../layout/NavAndSideBarLayout"
import {
  Button,
  Card,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core"
import { Link, navigate } from "gatsby"

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
  },
  link: {
    color: "inherit",
    textDecoration: "none",
  },
}))

interface Props {
  pageContext: PageContext
}

interface PageContext {
  slug: string
  slugsInfos: SlugsInfo[]
}

const IndexTemplate: React.FC<Props> = ({ pageContext }: Props) => {
  const classes = useStyles()

  useEffect(() => {
    const redirectRoute = pageContext.slugsInfos.sort(
      (a: SlugsInfo, b: SlugsInfo) => b.year - a.year,
    )[0].slug
    navigate(redirectRoute)
  }, [])

  return (
    <NavAndSideBarLayout
      slugsInfos={pageContext.slugsInfos}
      currentSlug={pageContext.slug}
    >
      <></>
    </NavAndSideBarLayout>
  )
}

export default IndexTemplate
